import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { error } from 'protractor';
@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {
  user;
  baseURL = "http://" + environment.apiBaseUrl;
  useravatar = 'uploads/avatar/sample-1.jpg';
  modalStatus = false;
  editStatus = false;
  addStatus = false;
  delKey;
  editKey;
  data = [];
  formGroup: FormGroup;
  reader = new FileReader();
  formData = new FormData();
  fileChange = false;
  fileDish;
  fileURL;
  key;
  srcCol=['username','fullname','email','phone','status'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  
  ngOnInit(): void {
    //etc
    this.user = this.userService.getToken()['userInfo'];
    this.refresh();
    this.getItem();
  }
  applyFilter(arr){
    if(!this.key) return true;
    for(let i=0;i<this.srcCol.length;i++){
      if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
        return true;
      }
    }
    return false;
  }
  add() {
    this.addStatus = true;
  }
  edit(id) {
    this.editKey=this.findIndex(id,this.data);
    this.editStatus = true;
    let insertValue= this.data[this.editKey];
    delete insertValue.__v;
    this.formGroup.setValue(insertValue);
  }
  del(id) {
    this.delKey = this.findIndex(id,this.data);
  }
  findIndex(id,datas) {
   for(let i=0;i<datas.length;i++){
     if(datas[i]._id===id) return i;
   }
   return 0;
  }
  cancel() {
    this.refresh();
  }
  async save() {
    if (this.addStatus == true)
    {
      const result= await this.uploadAvatar();
      if(result==true)  this.addItem();
    }
    else if (this.editStatus == true)    {
      const result= await this.uploadAvatar();
      if(result==true) this.editItem();
    }
  }
  addItem() {
    this.userService.createCustomer(this.formGroup.value).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success');
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
        this.refresh();
        this.showSuccessToaster('error', '', err.error.message);
      }
    );
  }
  editItem() {
    this.userService.updateCustomer(this.formGroup.value).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
        this.refresh();
      }
    );
  }
  deleteItem() {
    this.userService.delCustomer(this.data[this.delKey]).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
       this.handleError(err)
        this.refresh();
        this.getItem();
      }
    );
  }
  getItem() {
    this.userService.getCustomer(this.user).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
  async uploadAvatar(){
    if(!this.fileChange) return true;
    const ns = this.formGroup.value.username;
    const filename = ns + '.' + this.fileDish.name.split('.')[this.fileDish.name.split('.').length - 1];;
    this.formData.append('file', this.fileDish, filename);
    const result= await this.userService.uploadAvatar(this.formData);
    if(result.error) this.handleError(result.error)
    else {
      this.formGroup.value.avatar=result.path;
      return true;
    }
 }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.fileDish = undefined;
    this.fileURL = undefined;
    this.fileChange = false;
    const useravatar='uploads/avatar/sample-'+Math.floor(Math.random()*9)+'.jpg';
    this.formData = new FormData();
    const accountRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z0-9]{11}$/;
    const intenationalPhone = /\+(9[976]\d|8[987530]\d|6[987]\d|5[90]\d|42\d|3[875]\d|2[98654321]\d|9[8543210]|8[6421]|6[6543210]|5[87654321]|4[987654310]|3[9643210]|2[70]|7|1)\d{1,14}$/;
    this.formGroup = this._formBuilder.group({
      _id: [''],
      username: ['', Validators.required],
      password: ['', Validators.required],
      fullname: ['', Validators.required],
      email: ['', Validators.email],
      phone: ['', Validators.pattern(intenationalPhone)],
      avatar: [useravatar],
      status: ['active', Validators.required],
    });
  }
  //for toastr
  handleError(err){
   if(err.status==504)  this.showSuccessToaster('error', '', 'server is not responsing.');
   else         this.showSuccessToaster('error', '', err.error.message);
  }
  showSuccessToaster(style, msgtopic, msgContent) {
    this.toastr.show(style, msgtopic, msgContent);
  }
  //for file manage
  selectFile(event) {
    this.fileChange = true;
    const mimeType = event.target.files[0].type;
    this.fileDish = event.target.files[0];
    this.reader.readAsDataURL(event.target.files[0]);
    this.reader.onload = (_event) => {
      this.fileURL = this.reader.result;
    };
    event.target.value="";
  }
  fileButtonClick() {
    document.getElementById('photo').click();
  }
  //for image
  getAvatarSource() {
    if (this.fileChange) return this.fileURL;
    else return this.baseURL + this.formGroup.value.avatar;
  }
  //for date
  getDateString(ss){
    const d=new Date(ss);
    let dmonth=String(d.getMonth());
    let ddate=String(d.getDate());
    if(dmonth.length<2) dmonth='0'+dmonth;
    if(ddate.length<2) ddate='0'+ddate;
    return d.getFullYear()+'-'+dmonth+'-'+ddate;
  }
}
